<template>
    <div :style="{width: displayWidth+'px', height: displayHeight+'px'}">
        <p>
            Proof of concept: {{ msg }}
        </p>
    </div>
</template>

<script>
import bus from '@/event_bus';
import store from '@/store';

/**
 * Example composent
 */

export default {
    name: 'bob',

    props: ['config'],

    computed: {
        displayWidth: function () {
            return (this.config.width ? this.config.width : 100);
        },
        displayHeight: function () {
            return (this.config.height ? this.config.height : 50);
        }
    },

    methods: {
        userConnected (e, extra) {
            console.log(arguments);
            console.log(store.getters.baseUser);
            console.log('from Bob: Hello #' + store.getters.baseUser.id);
        },
        userDataArrived: function (e) {
            console.log('from Bob data ', e);
            console.log('from Bob data', e.user);
            this.msg = 'Hello ' + e.user.login_name;
        }
    },

    mounted () {
        this.$emit('display-sized', this.displayWidth, this.displayHeight);
        bus.$on('user-data', this.userDataArrived);
        bus.$on('http-initialized', this.userConnected);
    },

    data () {
        return {
            msg: ''
        };
    }
};
</script>

<style scoped>
div {
    background: green
}
p {
    color: red;
}
</style>



